import Actions from './action_type';
import config from '../config';

export const getBooks = () => {
    console.log('action')
  return async (dispatch) => {
    const res = await fetch(`${config.SQLDB_URL}/books`);
    const json = await res.json();
    console.log(json)
    dispatch({
      type: Actions.GET_BOOK,
      payload: json,
    })
  }
}

export const addBooks = (data) => {
  console.log('add',data)
return async (dispatch) => {
  console.log('add fetch',data)
  await fetch(`${config.SQLDB_URL}/books/add`,{
    method: 'POST',
    headers:{'Content-Type':'application/json'},
    body: JSON.stringify(data)
  });
    
  dispatch({
    type: Actions.ADD_BOOK,
    payload: data,
  })
}
}


export const deleteBooks = (id) => {
  console.log('delete',id)
return async (dispatch) => {
  console.log('delete fetch',id)
  await fetch(`${config.SQLDB_URL}/books/delete/${id}`,{
    method: 'DELETE',
  });
    
  dispatch({
    type: Actions.DELETE_BOOK,
    payload: id,
  })
}
}

export const editBook = (data) => {
  console.log('edit',data.id)
return async (dispatch) => {
  console.log('edit fetch',data)
  await fetch(`http://localhost:3002/books/edit/${data.id}`,{
    method: 'PUT',
    headers:{'Content-Type':'application/json'},
    body: JSON.stringify(data)
  });
    
  dispatch({
    type: Actions.EDIT_BOOK,
    payload: data,
  })
}
}
 
