import React, {Component} from 'react';
import { connect } from "react-redux";
import {editAuthor} from "../action/author_action"
import { Redirect } from 'react-router-dom';



const mapStatetoProps= (state)=>{
  console.log('mapstatetoprops')
  console.log(state)
  return {
    author:state.author_reducer
  };
}

class Editauthor extends Component{
  
  constructor(props){
    super(props);
    let edit_element={}
    if(props.author!==null){
      edit_element = (props.author.filter(element=>element.id===+props.match.params.id))[0]  
      console.log(edit_element)
    } else {
      edit_element = {
        Name:'',
        Born:'',
        Details:'',
        Description:'',
        Died:'',
        img:'',
        }
    }
  console.log(edit_element)
  this.state=({
   
    edit_element
  })
  
  }

  componentWillReceiveProps(props){
    
      this.setState({
        edit_element:(props.author.filter(element=>element.id===+props.match.params.id))[0]  
      })
      
    } 
  
    handleChange=(e)=>{  
      let edit_element=this.state.edit_element
      edit_element[e.target.name]=e.target.value
      this.setState( {edit_element} )
    }


  handleEdit= ((event) => {
    event.preventDefault();
   
    this.props.editAuthor(this.state.edit_element)
    this.setState({
      redirect: true
    })
  

  })

    render(){
      if (this.state.redirect) {
        return <Redirect to='/authors'/>;
      }
        return(
          
      <form className="form" onSubmit={this.handleEdit}>
        
      <div className="form-group col-sm-6">
        <label>Name: </label>
        <input name="Name" type="text" className="form-control" defaultValue={this.state.edit_element.Name} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
        <label>Born:
        </label>
        <input name="Born" type="text" className="form-control" defaultValue={this.state.edit_element.Born} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
      <label>Details: </label>
        <input name="Details" type="text" className="form-control"defaultValue={this.state.edit_element.Details} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
        <label>Died:
        </label>
        <input name="Died" type="text" className="form-control" defaultValue={this.state.edit_element.Died} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
      <label>Description: </label>
        <input name="Description" type="text" className="form-control" defaultValue={this.state.edit_element.Description} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
        <label>Image:
        </label>
        <input name="img" type="text" className="form-control" defaultValue={this.state.edit_element.img} onChange={this.handleChange} />
      </div>
      
      <button type="Submit" value="submit" className="btn btn-primary">Edit</button>  
   </form>
        )
    }
}




export default connect(mapStatetoProps,{editAuthor})(Editauthor);