import React, {Component} from 'react';
import { connect } from "react-redux";
import {editBook} from "../action/book_action"
import { Redirect } from 'react-router-dom';

class Editbook extends Component{
constructor(props){
  super(props);
  let edit_element={}
  if(props.books!==null){
    edit_element = (props.books.filter(element=>element.id===+props.match.params.id))[0]  
    console.log(edit_element)
  } else {
    edit_element = {
      
      id:'',
      Name:'',
      Author:'',
      Details:'',
      Description:'',
      Genre:'',
      img:'',
      isbn:''
      }
  }
console.log(edit_element)
this.state=({
 
  edit_element
})

}

componentWillReceiveProps(props){
  
    this.setState({
      edit_element:(props.books.filter(element=>element.id===+props.match.params.id))[0]  
    })
    
  } 




  handleChange=(e)=>{  
    let edit_element=this.state.edit_element
    edit_element[e.target.name]=e.target.value
    this.setState( {edit_element} )
  }

  handleEdit= ((event) => {
    event.preventDefault();
    console.log(this.state);  
    this.props.editBook(this.state.edit_element)
    this.setState({
      redirect: true
    })
  

  })

    render(){
      if (this.state.redirect) {
        return <Redirect to='/books'/>;
      }
 console.log(this.state.edit_element)
        return(
         
      <form className="form" onSubmit={this.handleEdit}>
        <div className="form-group col-sm-6">
          <label>id: </label>
        <input name="id" type="text" className="form-control" defaultValue={this.state.edit_element.id} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
        <label>Name: </label>
        <input name="Name" type="text" className="form-control" defaultValue={this.state.edit_element.Name} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
        <label>Author:
        </label>
        <input name="Author" type="text" className="form-control" defaultValue={this.state.edit_element.a} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
      <label>Details: </label>
        <input name="Details" type="text" className="form-control" defaultValue={this.state.edit_element.Details} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
        <label>Genre:
        </label>
        <input name="Genre" type="text" className="form-control" defaultValue={this.state.edit_element.Genre} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
      <label>Description: </label>
        <input name="Description" type="text" className="form-control"defaultValue={this.state.edit_element.Description} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
        <label>Image:
        </label>
        <input name="img" type="text" className="form-control" defaultValue={this.state.edit_element.img} onChange={this.handleChange} />
      </div>
      <div className="form-group col-sm-6">
        <label>Isbn:
        </label>
        <input name="isbn" type="text" className="form-control" defaultValue={this.state.edit_element.isbn} onChange={this.handleChange} />
      </div>
      <button type="Submit" value="submit" className="btn btn-primary">Edit</button>  
   </form>
        )
    }
}

const mapStatetoProps= (state)=>{
  console.log('mapstatetoprops')
  console.log(state)
  return {
    books:state.book_reducer
  };
}

export default connect(mapStatetoProps,{editBook})(Editbook);