
import Actions from '../action/action_type';


console.log('reducer book')

const  getbooks=(state=null,action)=>{
console.log('inside reducer',action.type)
  switch(action.type){
    case Actions.GET_BOOK:
    console.log("getbook",action.payload)
    return action.payload;
    case Actions.ADD_BOOK:
    console.log('addbook',...state,action.payload)
    return [...state,action.payload];
    case Actions.DELETE_BOOK:
    let new_state=state.filter(element=>element.id!==+action.payload);
    return new_state;
    case Actions.EDIT_BOOK:
    console.log('editbook',...state,action.payload)
    let stateEdit=state.map(element=>{
      if(element.id===+action.payload.id){
        console.log(element.id)
        element=action.payload
      }
      return element;
    });
    console.log("editbooks",stateEdit)
    return stateEdit;
    default:
    return state
  }
}

 export default getbooks;