import React, {Component} from 'react';
import {Link} from "react-router-dom";
import { connect } from "react-redux";
import { deleteBooks } from "../action/book_action"


class Books extends Component{

  handleDelete= ((event) => {
    event.preventDefault();
    console.log(event.target.value)
    console.log(this.props);  
    this.props.deleteBooks(event.target.value)

  })
 

  render(){
    console.log('book rendering initial')
    console.log(this.props)
    if((this.props.books)==null){
        return 0;
    }
    return(
      <div  className="container-fluid">
       
          <nav className="nav-sidebar">
            <ul className="nav">
              <li className="active"><Link to="/addbook"><span className="glyphicon glyphicon-star"></span>AddBook</Link></li>
            </ul>
          </nav>
        
        {this.props.books.map(book=>{return(
        
       
 <div key={book.id}>
 <div  className="row border">
 <div className="table-active table-bordered">
 
<img className="media-left" src={`images/${book.img}`} alt="placehold.it/350x250"/>
</div>
<div className="col-md-6"><h4 className="">{book.Name}</h4>
 <p> {book.Description}</p>
  </div>
 <div className="col-md-3">
 <Link to={"/editbook/"+ book.id} type="button" value={book.id} className="btn btn-default btn-lg btn-block" >Edit</Link>
     <button id="delete" value={book.id} type="button" className="btn btn-default btn-lg btn-block"  onClick={this.handleDelete}>Delete </button>
   </div>
  </div>
 </div>
        //</div>
            
      )})}
          
        </div>  
    );
    
  }
}

const mapStatetoProps= (state,props)=>{
  console.log('mapstatetoprops')
  console.log(state,props)
  return {
    books:state.book_reducer
  };
}
