
import Actions from '../action/action_type';


console.log('reducer author')

const  getauthors=(state=null,action)=>{
console.log('inside reducer',action.type)
  switch(action.type){
    case Actions.GET_AUTHOR:
    console.log("getauthor",action.payload)
    return action.payload;
    case Actions.ADD_AUTHOR:
    console.log('addauthor',...state,action.payload)
    return [...state,action.payload];
    case Actions.DELETE_AUTHOR:
    let state1=state.filter(element=>element.id!==+action.payload);
    return state1;
    case Actions.EDIT_AUTHOR:
    console.log('editauthor',...state,action.payload)
    let stateEdit=state.map(element=>{
      if(element.id===+action.payload.id){
        console.log(element.id)
        element=action.payload
      }
      return element;
    });
    console.log("editauthors",stateEdit)
    return stateEdit;
    default:
    return state
  }
}

 export default getauthors;