import React, { Component } from "react";
import {BrowserRouter,Route,Switch} from "react-router-dom";
import './App.css';
import Header from './components/Header';
import Authors from './components/Authors';
import Books from './components/Books';
import Addbook from './components/Addbook';
import Addauthor from './components/Addauthor';
import Editbook from './components/Editbook';
import {getBooks} from './action/book_action';
import {getAuthors} from './action/author_action';
import { connect } from "react-redux";
import Editauthor from "./components/Editauthor";


class App extends Component {

  componentDidMount(){
   console.log('app')
    this.props.getBooks()
    this.props.getAuthors()
  }
  
  render() {
    
    return (
      <div className="App">
    
      <BrowserRouter>
      <div>
      <Header />
    
          <Switch>
          <Route  path="/books" component={Books}/>
          <Route  path="/editbook/:id" component={Editbook}/>
          <Route  path="/addbook" component={Addbook}/>
          <Route  path="/authors" component={Authors}/>
          <Route  path="/editauthor/:id" component={Editauthor}/>
          <Route  path="/addauthor" component={Addauthor}/>
            {/* <Route  path="/editauthor" render={props => <Editauthor author={this.state.author} {...props} />} /> */}
            {/* <Route  path="/addbook" render={props => <Addbook addbook={this.addbook} {...props} />} />
            <Route  path="/addauthor" render={props => <Addauthor addauthor={this.addauthor} {...props} />} />
             */}
          </Switch>
          </div>
        
          </BrowserRouter>
     

      </div>
    );
  }
}

export default connect(null,{getBooks,getAuthors})(App);