import React, {Component} from 'react';
import {Link} from "react-router-dom";
import { connect } from "react-redux";
import { deleteAuthors } from "../action/author_action";
class Authors extends Component{


  handleDelete= ((event) => {
    event.preventDefault();
    console.log(event.target.value)
    console.log(this.props);  
    this.props.deleteAuthors(event.target.value)

  })

render(){
  console.log(this.props)
  if((this.props)==null){
      return ;
  }
  return(
    <div className="container-fluid">
      
        <nav className="nav-sidebar">
            <ul className="nav">
              <li className="active"><Link to="/addauthor"><span className="glyphicon glyphicon-star"></span>AddAuthor</Link></li>
            </ul>
        </nav>
        { this.props.author.map(author=>{return(
        <div key={author.id}>
           <div  className="row border">
              <div className="table-active table-bordered">
              <img className="media-left" src={`images/${author.img}`} alt=""/>
              
              </div>
              <div className="col-md-6">
              <h4 className="">{author.Name}</h4>
              <p> {author.Description}</p>
               </div>
              <div className="col-md-3 ">
              <Link to={"/editauthor/"+ author.id} type="button" value={author.id} className="btn btn-default btn-lg btn-block">Edit</Link>
              <button id="delete" value={author.id} type="button" className="btn btn-default btn-lg btn-block"  onClick={this.handleDelete}>Delete </button>
            </div>
            </div>
        </div>       
      )})}
      </div>
   
  );    
}
}

const mapStatetoProps= (state)=>{
  console.log('mapstatetoprops')
  console.log(state)
  return {
    author:state.author_reducer
  };
}

export default connect(mapStatetoProps,{deleteAuthors})(Authors);