import React, {Component} from 'react';
import {addAuthors} from "../action/author_action";
import { connect } from "react-redux";


class Addauthor extends Component{
  
  handleChange=(e)=>{    
    this.setState({ [e.target.name]: e.target.value });  
  }

  handleSubmit= ((event) => {
    event.preventDefault();
    const data=this.state 
    this.props.addAuthors(data) 
  })
    render(){
        return(
          <form className="form"onSubmit={this.handleSubmit}>
          <div className="form-group col-sm-6">
              <label >id</label>
              <input name="id" type="text" className="form-control" id="id" placeholder="Enter name"onChange={this.handleChange} />
            </div>
            <div className="form-group col-sm-6">
              <label >Name</label>
              <input name="Name" type="text" className="form-control" id="name" placeholder="Enter name"onChange={this.handleChange} />
            </div>
            <div className="form-group col-sm-6">
              <label >Born</label>
              <input name="Born" type="number" className="form-control" id="born" placeholder="Enter name"onChange={this.handleChange} />
            </div>
            <div className="form-group col-sm-6">
              <label >Details</label>
              <input name="Details" type="text" className="form-control" id="details" placeholder="Enter name"onChange={this.handleChange} />
            </div>
            <div className="form-group col-sm-6">
              <label >Died</label>
              <input name="Died" type="text" className="form-control" id="died"  placeholder="Enter name"onChange={this.handleChange} />
            </div>
            <div className="form-group col-sm-6">
              <label>Description</label>
              <input name="Description" type="text" className="form-control" id="description"  placeholder="Enter name"onChange={this.handleChange} />
            </div>
            <div className="form-group col-sm-6">
                <label >image</label>
                <input name="img" type="text" className="form-control" id="img"  placeholder="Enter name"onChange={this.handleChange} />
            </div>
  
            <button type="submit" className="btn btn-primary">Add</button>

         </form>
        )
    }


}

export default  connect(null,{addAuthors})(Addauthor);