import Actions from './action_type';
import config from '../config';


export const getAuthors = () => {
  return async (dispatch) => {
    const res = await fetch(`${config.SQLDB_URL}/authors`);
    const json = await res.json();
    dispatch({
      type: Actions.GET_AUTHOR,
      payload: json,
    })
  }
}

export const addAuthors = (data) => {
  console.log('add',data)
return async (dispatch) => {
  console.log('add fetch',data)
  await fetch(`${config.SQLDB_URL}/authors/add`,{
    method: 'POST',
    headers:{'Content-Type':'application/json'},
    body: JSON.stringify(data)
  });
    
  dispatch({
    type: Actions.ADD_AUTHOR,
    payload: data,
  })
}
}


export const deleteAuthors = (id) => {
  console.log('delete',id)
return async (dispatch) => {
  console.log('delete fetch',id)
  await fetch(`${config.SQLDB_URL}/authors/delete/${id}`,{
    method: 'DELETE',
  });
    
  dispatch({
    type: Actions.DELETE_AUTHOR,
    payload: id,
  })
}
}

export const editAuthor = (data) => {
  console.log('edit',data.id)
return async (dispatch) => {
  console.log('edit fetch',data)
  await fetch(`${config.SQLDB_URL}/authors/edit/${data.id}`,{
    method: 'PUT',
    headers:{'Content-Type':'application/json'},
    body: JSON.stringify(data)
  });
    
  dispatch({
    type: Actions.EDIT_AUTHOR,
    payload: data,
  })
}
}
